@import url('https://fonts.googleapis.com/css?family=Inconsolata:700');

.search-container {
  position: absolute;
  top: -30px;
  left: 0;
  right: 0;
  width: 350px;

  .search {
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 50px;
    height: 50px;
    background: #29a745;
    border-radius: 50%;
    transition: all 1s;
    z-index: 4;
    box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.4);
    &:hover {
      cursor: pointer;
    }
    &::before {
      content: "";
      position: absolute;
      margin: auto;
      top: 22px;
      right: 0;
      bottom: 0;
      left: 22px;
      width: 12px;
      height: 2px;
      background: white;
      transform: rotate(45deg);
      transition: all .5s;
    }
    &::after {
      content: "";
      position: absolute;
      margin: auto;
      top: -5px;
      right: 0;
      bottom: 0;
      left: -5px;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      border: 2px solid white;
      transition: all .5s;
    }
  }
  input {
    font-family: 'Inconsolata', monospace;
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 40px;
    height: 40px;
    outline: none;
    border: none;
    background: rgb(0, 0, 0);
    color: white;
    text-shadow: 0 0 10px #29a745;
    padding: 0 0 0 20px;
    border-radius: 30px;
    box-shadow: 0 0 25px 0 #29a745,
                0 20px 25px 0 rgba(0, 0, 0, 0.2);
    transition: all 1s;
    opacity: 0;
    z-index: 5;
    font-weight: bolder;
    letter-spacing: 0.1em;
    &:hover {
      cursor: pointer;
    }
    &:focus {
      width: 300px;
      opacity: 1;
      cursor: text;
    }
    &:focus ~ .search {
      right: -250px;
      background: #151515;
      z-index: 6;
      &::before {
        top: 0;
        left: 0;
        width: 25px;
      }
      &::after {
        top: 0;
        left: 0;
        width: 25px;
        height: 2px;
        border: none;
        background: white;
        border-radius: 0%;
        transform: rotate(-45deg);
      }
    }
    &::placeholder {
      color: white;
      opacity: 0.5;
      font-weight: bolder;
    }
  }

  .sort {
    width: 30px;
    height: 30px;
    color: white;
    background: #29a745;
    position: absolute;
    top: 10px;
    left: 60px;
    border-radius: 50%;
    border: 2px solid white;
  }
}

@media (max-width: 880px) {
  .search-container {
    top: 30px;
    left: -30px;
    right: 0;
    width: 365px;
  }
}

@media (max-width: 350px) {
  .search-container {
    top: 30px;
    left: -30px;
    right: 0;
    width: 300px;

    input {
      &:focus {
        width: 230px;
      }
    }
  }
}